import { CalendarDaysIcon, CheckIcon, UsersIcon } from '@heroicons/vue/20/solid'

import { BookingStatus, type TourBooking } from '@/shared/types'
import { DepartureProgressType, type RatioItem } from '@/shared/ui/PMultiRatioBar'
import { type ClassValue, cn } from '@/shared/lib/style'
import { SeatIcon } from '@/shared/ui/icons'

export function calculateDepartureArrivedPassengers(bookings: TourBooking[]) {
  return bookings.reduce(
    (acc, b) => {
      const fallbackValue = b.status === BookingStatus.Confirmed
        ? b.totalPassengerCount
        : 0

      return acc + (b.arrivedPassengerCount ?? fallbackValue)
    },
    0,
  )
}

export function produceDepartureRatioItems(
  bookingSystemAvailability: number | null,
  passengers: number,
  seats: number | null,
): RatioItem[] {
  const availability = bookingSystemAvailability ?? 0
  // Method for deciding which item should be centered
  // If availability is greater than 0, center the availability item
  // Otherwise, center the passengers item
  // if seats are also 0 (e.g. no seats), center the passengers item
  // TODO: remove nested ternary
  const centeredItem = availability > 0
    ? DepartureProgressType.Availability
    : seats && seats > 0
      ? DepartureProgressType.Seats
      : DepartureProgressType.Passengers

  // TODO: remove nested ternary
  const centeredValue
    = centeredItem === DepartureProgressType.Availability
      ? availability
      : seats && centeredItem === DepartureProgressType.Seats
        ? seats
        : passengers

  const availabilityEqualsSeats = availability === seats
  const availabilityEqualsPassengers = availability === passengers
  const availabilityEqualsSeatsAndPassengers = (
    availability === seats && availability === passengers
  )

  let seatsColor: ClassValue = cn([
    'border-gray-600 text-gray-600',
    'dark:border-gray-400 dark:text-gray-300',
  ])

  // SEATS LOGIC
  // Condition 1: Check if there are extra seats compared to availability
  if (seats && seats > availability && availability > 0) {
    // seatsNotificationType = DepartureMessageType.Secondary
  }

  // Condition 2: Check if there are extra seats compared to passengers
  if (
    seats && seats > passengers && passengers >= availability && availability > 0
  ) {
    seatsColor = cn([
      'border-indigo-600 text-indigo-600',
      'dark:border-blue-500 dark:text-blue-500',
    ])
  }

  // Condition 3: Check if seats are less than availability
  if (
    seats && seats > passengers && seats < availability && availability > 0
  ) {
    seatsColor = cn('border-orange-600 text-orange-600')
  }

  // Condition 4: Check if there are not enough seats for passengers.
  if (seats && seats < passengers) {
    seatsColor = cn('border-red-600 text-red-600')
  }

  // Condition 5: Check if there are not enough seats
  // when availability is greater than 0.
  if (seats && seats < passengers && availability > 0) {
    seatsColor = cn('border-red-600 text-red-600')
  }

  // Condition 6: Check if there are no seats assigned.
  if (seats === 0) {
    seatsColor = cn('border-red-600 text-red-600')
  }

  let passengersColor: ClassValue = cn([
    'border-gray-600 text-gray-600',
    'dark:border-gray-400 dark:text-gray-300',
  ])
  let passengersIcon: RatioItem['icon'] = UsersIcon

  // Passengers logic
  // Condition 7: Check if availability matches seats
  // and there are more passengers than availability.
  if (availability === seats && availability < passengers) {
    passengersColor = cn([
      'border-red-600 text-red-600',
      'dark:border-red-500 dark:text-red-500',
    ])
  }

  // Condition 8: Check if bookingSystemAvailability is null.
  if (bookingSystemAvailability === null) {
    passengersColor = cn([
      'border-gray-600 text-gray-600',
      'dark:border-gray-400 dark:text-gray-300',
    ])
  }

  // Condition 9: Check if availability is 0 and passengers equal seats.
  if (availability === 0 && passengers === seats) {
    passengersColor = cn([
      'border-green-600 text-green-600',
      'dark:border-green-400 dark:text-green-300',
    ])
    passengersIcon = CheckIcon
  }

  let availabilityColor: ClassValue = cn([
    'border-gray-600 text-gray-600',
    'dark:border-gray-400 dark:text-gray-300',
  ])
  let availabilityIcon: RatioItem['icon'] = CalendarDaysIcon

  // Availability logic
  // Condition 10: Check if there are overbooked passengers
  // compared to availability.
  if (availability < passengers) {
    availabilityColor = cn([
      'border-red-600 text-red-600',
      'dark:border-red-500 dark:text-red-500',
    ])
  }

  // Condition 11: Check if availability equals seats
  // but is less than passengers.
  if (
    availability === seats && availability < passengers
  ) {
    availabilityColor = cn([
      'border-gray-600 text-gray-600',
      'dark:border-gray-400 dark:text-gray-300',
    ])
  }

  // Condition 12: Check if availability equals seats and passengers
  // or availability is null and seats equal passengers.
  if (
    availabilityEqualsSeatsAndPassengers
    || (availability === null && seats === passengers)
  ) {
    availabilityColor = cn('border-green-600 text-green-600')
    availabilityIcon = CheckIcon
  }

  // Condition 13: Check if availability matches passengers
  // or availability matches seats.
  if (availabilityEqualsPassengers || availabilityEqualsSeats) {
    availabilityIcon = CheckIcon
  }

  const ratioItems: RatioItem[] = []

  if (availability > 0) {
    const availabilityRatioItem: RatioItem = {
      key: DepartureProgressType.Availability,
      color: availabilityColor,
      value: availability,
      icon: availabilityIcon,
      barPosition: 50, // centered is default position
    }

    ratioItems.push(availabilityRatioItem)
  }

  if (seats !== passengers && seats !== null) {
    const seatsRatioItem: RatioItem = {
      key: DepartureProgressType.Seats,
      color: seatsColor,
      value: seats,
      icon: SeatIcon,
      barPosition: (seats / centeredValue * 100) - 50,
      isDisplayedInEditMode: seats <= 0,
    }

    ratioItems.push(seatsRatioItem)
  }

  if (!availabilityEqualsPassengers && passengers > 0) {
    const passengersRatioItem: RatioItem = {
      key: DepartureProgressType.Passengers,
      color: passengersColor,
      value: passengers,
      icon: passengersIcon,
      barPosition: (passengers / centeredValue * 100) - 50,
    }

    ratioItems.push(passengersRatioItem)
  }

  return ratioItems
}
