<script setup lang="ts">
import { computed, toRefs } from 'vue'

import { ensure } from '@/shared/lib/utils'

import type { DepartureProgressType, RatioItem } from './types'

export type Props = {
  centeredValue: DepartureProgressType
  ratioItems: RatioItem[]
}

const props = defineProps<Props>()
const { centeredValue, ratioItems } = toRefs(props)

const centeredRatioItem = computed(() =>
  ensure(ratioItems.value.find((i) => i.key === centeredValue.value)),
)

const spaceNeeded = 15 // space needed for each item
const centeredValueBarPosition = computed(() =>
  centeredRatioItem.value.barPosition,
)

const computedRatioItems = computed(() => {
  const barPositions = ratioItems.value.map((item) => {
    return {
      key: item.key,
      barPosition: item.barPosition,
      value: item.value,
      color: item.color,
      icon: item.icon,
    }
  })
  barPositions.forEach((value) => {
    if (value.key === centeredValue.value) {
      return
    }
    // check if the centered item is intersecting with any item
    if (value.barPosition >= centeredValueBarPosition.value
      && value.barPosition <= centeredValueBarPosition.value + spaceNeeded) {
      value.barPosition = centeredValueBarPosition.value + spaceNeeded
    }
    if (value.barPosition <= centeredValueBarPosition.value
      && value.barPosition > centeredValueBarPosition.value - spaceNeeded) {
      value.barPosition = centeredValueBarPosition.value - spaceNeeded
    }
    if (value.barPosition < 0) {
      value.barPosition = 0
    }
    if (value.barPosition > 100) {
      value.barPosition = 100
    }

    // check if all other items are not intersecting
    barPositions.forEach((value2) => {
      if (value.key === value2.key) {
        return
      }
      if (value.barPosition >= value2.barPosition
        && value.barPosition <= value2.barPosition + spaceNeeded
        && value.barPosition <= centeredValueBarPosition.value) {
        value.value >= value2.value
          ? value2.barPosition -= spaceNeeded
          : value.barPosition -= spaceNeeded
      }
      if (value.barPosition <= value2.barPosition
        && value.barPosition >= value2.barPosition - spaceNeeded
        && value.barPosition >= centeredValueBarPosition.value) {
        value.value >= value2.value
          ? value.barPosition += spaceNeeded
          : value2.barPosition += spaceNeeded
      }
      if (value2.barPosition < 0) {
        value2.barPosition = 0
      }
      if (value.barPosition === 0
        && (value2.barPosition === 0
        || (value2.barPosition <= value.barPosition + spaceNeeded
        && value2.barPosition >= value.barPosition))) {
        value.value > value2.value
          ? value.barPosition += spaceNeeded
          : value2.barPosition += spaceNeeded
      }
      if (value2.barPosition > 100) {
        value2.barPosition = 100
      }
      if (value.barPosition === 100
        && (value2.barPosition === 100
        || (value2.barPosition <= value.barPosition - spaceNeeded
        && value2.barPosition >= value.barPosition))) {
        value.value > value2.value
          ? value2.barPosition -= spaceNeeded
          : value.barPosition -= spaceNeeded
      }
    })
  })

  return barPositions.map((p) => ({
    ...p,
    left: `calc(${p.barPosition}% - 14px)`,
  }))
})
</script>

<template>
  <div class="relative h-16 min-w-44">
    <div
      v-for="{ key, color, left, icon, value } in computedRatioItems"
      :key="key"
      class="absolute w-7"
      :style="{ left }"
    >
      <div
        class="
        flex h-7 w-full items-center justify-center rounded-full
        border transition-all duration-1000
        "
        :class="color"
      >
        <Component :is="icon" class="h-4 w-4" />
      </div>

      <p class="absolute top-10 w-full text-center" :class="color">
        {{ value }}
      </p>
    </div>

    <div
      class="
      absolute top-8 h-1.5 w-full rounded-full shadow-[inset_1px_1px_4px_rgba(0,0,0,0.1)]
     "
    />
  </div>
</template>
